html, body {
    width: 100%;
    height: 100%;
    background: #020C15;
}
h2, h5 {
    color: #FFF8F0;
}
.badge-secondary {
    color: #FFF8F0;
    background: #26a67e;
}
.badge {
    margin-left: 10px;
}
.fade-in {
    -webkit-animation: fadein 2s;
    animation: fadein 2s;
}
.loading-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 90vh;
}
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}
@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}



